body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.navbar-brand{
  font-size: 2rem;
}


/* Home page Styling Start */
.home-page{
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 40vh;
}

/* Home page Styling End */

/* Content :  Main To Do container Styling Start */
.main-to-do-container{
  margin-bottom: 10px;
}
/* Content : Main To Do container Styling End */

/* Footer Styling */
footer{
  position: relative;
  bottom: 0;
}
/* Footer Styling end */



.main-heading{
  color: blueviolet;
  font-size: 2.5rem;
  text-align: center;
  margin-top: 2rem;
  margin-bottom: 2rem;
}

.main-grid{
  /* border: 1px solid red; */
  display: grid;
  grid-template-rows: 1fr 1fr 1fr 1fr;
  grid-template-columns: 1fr 1fr 1fr 1fr;
  text-align: center;
}

.main-grid-heading{
  font-size: 4rem;
  grid-row: span 3;
  grid-column: span 4;
}

.main-grid-element{
  grid-column-start: 2;
  grid-column-end: 4;
  /* border: 1px solid red; */
}

.main-grid-element a{
  text-decoration: none;
  margin: 0 2rem;
}


.tasks{
  display: flex;
  gap: 2rem;
  margin-bottom: 1rem;
  background-color: rgba(0, 0, 0, 0.194);
  text-align: center;
}
